@import '../../index.scss';


.tour{
    background: $mainGray;
    margin:2rem 0;
    img{
        width:100%;
        display: block;
            min-height: 13rem;
                
    }
}

@media screen and (min-width:768px){
.tour{
    display: grid;
    grid-template-columns:2fr 3fr;
    grid-column-gap: 1rem;
}
}

.img-container{
    position: relative;
}

.close-btn{
    position: absolute;
    display:block;
    top: 2%;
    right:2%;
    color: $mainGreen;
    font-size:2rem;
    cursor: pointer;
    transition: all 0.3s linear;
    opacity: 0;

}

.img-container:hover .close-btn{
    opacity: 1;
}
.tour-info{
    padding: 1rem 0.5rem;
    h3{
        text-transform: uppercase;
        color: $mainGreen;
    }

    h4{
        text-transform: capitalize;
         margin:0.2rem 0 0.6rem 0;
    } 
          h5{
              text-transform: capitalize; 
              font-size: 1rem;
              span{
                  color: $mainGreen;
                  margin: 0.5rem ;
                  font-size: 1.2rem;
                  cursor: pointer;
              }
          }
}